import { GoogleMap, Marker, useLoadScript } from "@react-google-maps/api";
import React, { forwardRef, useImperativeHandle, useState } from "react";
import Autocomplete from "react-google-autocomplete";
import { getEnvVariableValue } from "utils/CommonUtil";
import { ENV_VAR } from "utils/Constant";

interface Place {
  lat: number;
  lng: number;
}

export interface IPlacesRef {
  getLatLong: () => Place;
}

const Places = forwardRef<IPlacesRef, any>((props, ref) => {
  const { latLong } = props;
  const apiKey = getEnvVariableValue(ENV_VAR.VITE_GOOGL_MAP_KEY);
  const { isLoaded } = useLoadScript({
    googleMapsApiKey: apiKey,
    libraries: ["places"],
  });
  console.log("latLong", latLong);

  const [selected, setSelected] = useState<Place>({
    lat: parseFloat(latLong?.latitute) ?? 12.9716,
    lng: parseFloat(latLong.longitude) ?? 77.5946,
  });

  useImperativeHandle(ref, () => ({
    getLatLong: () => {
      return selected;
    },
  }));

  if (!isLoaded) return <div>Loading...</div>;
  const handlePlaceSelected = (place: any) => {
    if (place.geometry && place.geometry.location) {
      const { lat, lng } = place.geometry.location;
      setSelected({ lat: lat(), lng: lng() });
    } else {
      console.error("Invalid place object:", place);
    }
  };
  return (
    <>
      <div className="places-container">
        <Autocomplete
          apiKey={apiKey}
          options={{
            types: [],
            // types: ["(regions)"]
            // types: ["address"]
          }}
          onPlaceSelected={handlePlaceSelected}
          style={{ width: "250px" }}
        />
      </div>
      <GoogleMap zoom={18} center={selected} mapContainerClassName="map-container">
        <Marker
          position={selected}
          draggable={true}
          onDragEnd={(e: any) => {
            if (e.latLng?.lat && e.latLng?.lng) {
              setSelected({ lat: e.latLng.lat(), lng: e.latLng.lng() });
            }
          }}
        />
      </GoogleMap>
    </>
  );
});

export default Places;
