import ButtonBox from "components/ButtonBox/ButtonBox";
import DialogBox from "components/Dialog/Dialog";
import Places, { IPlacesRef } from "components/Places/Places";
import React, { forwardRef, useEffect, useImperativeHandle, useRef } from "react";
import { BUTTON_TYPE } from "utils/Constant";

export interface ILatLong {
  latitute: string;
  longitude: string;
}
export interface IMapRef {
  show: () => void;
}
interface IMapProps {
  showMap: boolean;
  setLatLong: (mapData?: ILatLong | undefined) => void;
  latLong?: ILatLong;
}

const Map = forwardRef<IMapRef, IMapProps>((props, ref) => {
  const { setLatLong } = props;
  const [showDialog, setShowDialog] = React.useState<boolean>(false);
  const [showMap, setShowMap] = React.useState(props.showMap);
  const [latLong, setLatLng] = React.useState<any>({
    latitute: 12.9716,
    longitude: 77.5946,
  });

  const placeRef = useRef<IPlacesRef>(null);

  useEffect(() => {
    setLatLng({
      latitute: props.latLong?.latitute ? props.latLong.latitute : 12.9716,
      longitude: props.latLong?.longitude ? props.latLong.longitude : 77.5946,
    });
  }, [props.latLong]);

  useImperativeHandle(ref, () => ({
    show: onShowDialog,
  }));

  const onShowDialog = () => {
    setShowDialog(true);
    setShowMap(true);
  };
  const onHideDialog = () => {
    setShowDialog(false);
  };

  const onSave = () => {
    const latLong = placeRef.current?.getLatLong();
    const data = {
      latitute: `${latLong?.lat.toFixed(4) ?? "0"}`,
      longitude: `${latLong?.lng.toFixed(4) ?? "0"}`,
    };
    setLatLong(data);
    onHideDialog();
  };
  // console.log("latLong", latLong);

  return (
    <>
      {showMap ? (
        <DialogBox
          modal
          dismissableMask
          visible={showDialog}
          onHide={onHideDialog}
          className=""
          style={{ width: "50vw" }}
        >
          <div className="flex flex-col gap-4 relative">
            {/* <Text
              label="label.heading.selectLocation"
              className="text-primary-ptext-1000 text-base font-Roboto font-bold flex"
            /> */}
            <Places ref={placeRef} latLong={latLong} />
            <div className="flex justify-center gap-6">
              <ButtonBox buttonType={BUTTON_TYPE.CANCEL_OUTLINE} onClick={onHideDialog} />
              <ButtonBox buttonType={BUTTON_TYPE.SAVE_OUTLINE} onClick={onSave} />
            </div>
          </div>
        </DialogBox>
      ) : (
        <> </>
      )}
    </>
  );
});

export default Map;
