// --------- ---------- //
import locationIcon from "assets/img/buttonIcons/location.svg";
import locationDisabledIcon from "assets/img/buttonIcons/location_disabled.svg";
// --------- ---------- //
import AclGuard from "components/AclGuard";
import AutoComplete from "components/AutoComplete";
import ErrorDialog from "components/ErrorDialog";
import InputBox from "components/InputBox";
import InputTextArea from "components/InputTextArea";
import Map, { IMapRef } from "components/Map";
import SimpleDropDown from "components/SimpleDropDown";
import Text from "components/Text";
import TextButton from "components/TextButton";
import ToastCustom from "components/Toast";
import { ToastPositionType } from "primereact/toast";
import React, {
  ChangeEvent,
  forwardRef,
  useEffect,
  useImperativeHandle,
  useRef,
  useState,
} from "react";

// --------- ---------- //
import CityService from "services/masterdata/Location/City";
import PincodeService from "services/masterdata/Location/Pincode";
import StaticMasterDataService from "services/masterdata/StaticMaster/StaticMasterDataService";
// --------- ---------- //
import SimpleReactValidator from "simple-react-validator";
import { getStandardValidator, messageReplace, validateGetAPIResponse } from "utils/CommonUtil";

// --------- ---------- //
import {
  INPUT_STYLE_TYPE,
  PAGE_MODE,
  STATIC_MASTER_TYPE_TOAST_TEXT,
  TOAST_STYLE_TYPE,
  VARIANTS,
} from "utils/Constant";

export interface IAddressRef {
  save: any;
  resetValidator: any;
  clear: any;
  getCurrentAddress: () => IAddressData;
  searchAutoCompletePincode: (event: { query: string }) => Promise<void>;
}

export interface IAddressData {
  uid?: string | null;
  address: string;
  pincode: { pinCode: string; uid: string };
  city: { name: string; uid: string };
  state: string;
  latitude: string;
  longitude: string;
  smAddressType?: string;
  primaryAddressType?: string;
  propertyType?: string;
}

interface IAddressProps {
  pageMode: string;
  addressTextAreaClassName?: string;
  addressPlaceholder: string;
  addressLabel?: string;
  addressMaxCount?: number;
  children: React.ReactNode;
  data?: IAddressData;
  variant: "PRIMARY" | "SECONDARY" | "TERTIARY" | "QUATERNARY";
  skipValidation?: boolean;
  isLatLong?: boolean;
  hideMarkUsingMap?: boolean;
  onAddressChange?: (data: IAddressData) => void;
}

const AddressWithoutMemo: React.FC<any> = forwardRef<IAddressRef, IAddressProps>((props, ref) => {
  const {
    pageMode,
    data,
    variant,
    addressTextAreaClassName,
    addressLabel,
    addressPlaceholder,
    addressMaxCount,
    isLatLong,
    skipValidation,
    onAddressChange,
    hideMarkUsingMap,
  } = props;

  const [pageObj, setPageObj] = useState<IAddressData>({
    uid: data?.uid ?? null,
    address: data?.address ?? "",
    pincode: { pinCode: data?.pincode?.pinCode ?? "", uid: data?.pincode?.uid ?? "" },
    city: { name: data?.city?.name ?? "", uid: data?.city?.uid ?? "" },
    state: data?.state ?? "",
    latitude: data?.latitude ?? "",
    longitude: data?.longitude ?? "",
    smAddressType: data?.smAddressType ?? "",
    propertyType: data?.propertyType ?? "",
  });

  const [position, setPosition] = useState<ToastPositionType>("bottom-right");
  const [isPincodeLoading, setIsPincodeLoading] = useState<boolean>(false);
  const [isCityLoading, setIsCityLoading] = useState<boolean>(false);
  const [toastMessage, setToastMessage] = useState<string>("");
  const [triggerToast, setTriggerToast] = useState<boolean>(false);
  const [toastMessageVariant, setToastMessageVariant] = useState<string>(TOAST_STYLE_TYPE.PRIMARY);
  const toastMessageRef = useRef<any>(null);
  const [errorMessage, setErrorMessage] = useState<string>("");
  const [showDialogAPIError, setShowDialogAPIError] = useState<boolean>(false);
  const [suggestionsTemplate, setSuggestionsTemplate] = useState<
    {
      name: string;
      code: string;
    }[]
  >([]);
  const [propertyTypeOptions, setPropertyTypeOptions] = useState<any[]>([]);
  const [showMap, setShowMap] = useState<boolean>(false);
  const mapRef = useRef<IMapRef>(null);
  const [addressList, setAddressList] = useState<any[]>([]);

  // --------------------------- validator initialization ---------------------- //

  const [, forceUpdate] = useState<boolean>(false);
  const simpleValidator = useRef(
    new SimpleReactValidator({
      validators: { ...getStandardValidator() },
      ...messageReplace(),
      autoForceUpdate: { forceUpdate: () => forceUpdate((prev) => !prev) },
    })
  );

  /*
    --------------------------------------------------------------------------------------------------
    LifeCycle Methods: 
    --------------------------------------------------------------------------------------------------
  */

  useEffect(() => {
    if (toastMessageRef?.current && toastMessage !== "") {
      // @ts-ignore: will fix later
      toastMessageRef?.current?.showFunction();
    }
  }, [toastMessage, toastMessageVariant, triggerToast]);

  useEffect(() => {
    if (data && !data.state && data?.city?.name) {
      getStateName();
    }
    if (data && Object.keys(data).length) {
      setPageObj({
        uid: data?.uid ?? null,
        address: data?.address ?? "",
        pincode: { pinCode: data?.pincode?.pinCode ?? "", uid: data?.pincode?.uid ?? "" },
        city: { name: data?.city?.name ?? "", uid: data?.city?.uid ?? "" },
        state: data?.state ?? "",
        latitude: data?.latitude ?? "",
        longitude: data?.longitude ?? "",
        smAddressType: data?.smAddressType ?? "",
        propertyType: data?.propertyType ?? "",
      });
      // for pre -populating  pincode in qde
      if (data?.pincode?.pinCode?.length > 0 && !data?.pincode?.uid) {
        searchAutoCompletePincode({ query: data?.pincode?.pinCode, selectFirstOption: true });
      }
    }
  }, [data]);

  useEffect(() => {
    if (props.variant === VARIANTS.TERTIARY) {
      getPropertyTypeList();
    }
    getResidentialAddressList();
  }, []);

  /*
    --------------------------------------------------------------------------------------------------
    Ref: For creating and returning properties
    --------------------------------------------------------------------------------------------------
  */

  useImperativeHandle(ref, () => ({
    save: onSave,
    resetValidator: resetValidator,
    clear: onClear,
    getCurrentAddress: getCurrentAddress,
    searchAutoCompletePincode: searchAutoCompletePincode,
    getCurrentValues: getCurrentValues,
  }));

  /*
    -------------------------------------------------------------------------------------------------------
    Service Calls: For Getting Data from service/API
    -------------------------------------------------------------------------------------------------------
  */

  const getResidentialAddressList = () => {
    const search = `staticMasterType.code=ADDRESS_TYPE`;
    const start = 0;
    const limit = 200;
    StaticMasterDataService.getAll(start, limit, search)
      .then((res) => {
        if (validateGetAPIResponse(res)) {
          setAddressList(res?.data?.results);
        }
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const searchAutoCompletePincode = async (event: any) => {
    let suggestionsTemplate: any[] = [];
    setIsPincodeLoading(true);
    try {
      await PincodeService.findByName(0, 10, event.query).then((res) => {
        if (validateGetAPIResponse(res)) {
          suggestionsTemplate = [...res.data.results];
          setIsPincodeLoading(false);
          if (event.selectFirstOption && suggestionsTemplate.length > 0) {
            setPageObj((prevObj) => ({
              ...prevObj,
              pincode: { ...prevObj.pincode, uid: suggestionsTemplate?.[0]?.uid ?? "" },
            }));
          }
        }
      });
    } catch (error) {
      setPosition("top-right");
      setToastMessageVariant(TOAST_STYLE_TYPE.QUATERNARY);
      setToastMessage(STATIC_MASTER_TYPE_TOAST_TEXT.ERROR);
      setTriggerToast(!triggerToast);
      setErrorMessage(STATIC_MASTER_TYPE_TOAST_TEXT.ERROR);
    }

    if (suggestionsTemplate.length === 0) {
      suggestionsTemplate.push({ name: "", code: "", uid: "" });
    }

    setSuggestionsTemplate(suggestionsTemplate);
  };

  const searchAutoCompleteCity = async (event: any) => {
    let suggestionsTemplate = [];
    setIsCityLoading(true);
    try {
      await CityService.findByName(0, 10, event.query).then((res) => {
        if (validateGetAPIResponse(res)) {
          suggestionsTemplate = [...res.data.results];
          setIsCityLoading(false);
        }
      });
    } catch (error) {
      setPosition("top-right");
      setToastMessageVariant(TOAST_STYLE_TYPE.QUATERNARY);
      setToastMessage(STATIC_MASTER_TYPE_TOAST_TEXT.ERROR);
      setTriggerToast(!triggerToast);
    }

    if (suggestionsTemplate.length === 0) {
      suggestionsTemplate.push({ name: "", code: "", uid: "" });
    }

    setSuggestionsTemplate(suggestionsTemplate);
  };

  const getStateName = async () => {
    try {
      await CityService.findByName(0, 10, data?.city.name).then((res) => {
        if (validateGetAPIResponse(res)) {
          setPageObj((prevState: any) => {
            return { ...prevState, state: res?.data?.results?.[0]?.state?.name };
          });
        }
      });
    } catch (error) {
      console.log(error);
    }
  };

  const getPropertyTypeList = async () => {
    try {
      const res = await StaticMasterDataService.getDropdownList("PROPERTY_TYPE");
      setPropertyTypeOptions(res?.data?.results);
    } catch (err) {
      console.error(err);
    }
  };

  /* 
    -----------------------------------------------------------------------------------------------------------------------
    Validators:
    -----------------------------------------------------------------------------------------------------------------------
  */

  const validate = (e: any) => {
    simpleValidator?.current?.showMessageFor(e?.target?.name);
  };

  const validateForm = () => {
    let isValid = false;

    if (skipValidation) {
      return pageObj;
    }

    isValid = simpleValidator?.current?.allValid();
    if (isValid) {
      return pageObj;
    } else {
      forceUpdate((prev) => !prev);
      simpleValidator?.current?.showMessages();
      forceUpdate((prev) => !prev);
      return false;
    }
  };

  const resetValidator = () => {
    simpleValidator.current = new SimpleReactValidator({
      validators: { ...getStandardValidator() },
      ...messageReplace(),
      autoForceUpdate: { forceUpdate: () => forceUpdate((prev) => !prev) },
    });
    simpleValidator?.current?.hideMessages();
  };

  const onClear = () => {
    const prevData = { ...pageObj };
    setPageObj({
      uid: null,
      address: "",
      pincode: { pinCode: "", uid: "" },
      city: { name: "", uid: "" },
      state: "",
      latitude: "",
      longitude: "",
      smAddressType: "",
      propertyType: "",
    });
    return prevData;
  };

  /*
    -----------------------------------------------------------------------------------------------
    Handlers: Event handler for onChange, onBlur etc
    -----------------------------------------------------------------------------------------------
  */

  const handleFilterChange = (event: ChangeEvent<HTMLInputElement> | any) => {
    const { name, value } = event.target;

    if (typeof value === "object") {
      setPageObj((prevPageObj: any) => {
        const updatedPageObj = {
          ...prevPageObj,
          pincode: { pinCode: value?.pinCode, uid: value?.uid },
        };
        onAddressChange?.({ ...updatedPageObj });
        return { ...updatedPageObj };
      });
    } else {
      setPageObj((prevPageObj) => {
        const updatedPageObj = {
          ...prevPageObj,
          [name]: { pinCode: value, uid: "" },
        };
        onAddressChange?.({ ...updatedPageObj });
        return { ...updatedPageObj };
      });
    }
  };

  const handleFilterCityStateChange = (event: ChangeEvent<HTMLInputElement> | any) => {
    const { name, value } = event.target;
    if (typeof value === "object") {
      setPageObj((prevPageObj: any) => {
        const updatedPageObj = {
          ...prevPageObj,
          [name]: { name: value?.name, uid: value?.uid },
          state: value?.state?.name,
        };
        onAddressChange?.({ ...updatedPageObj });
        return { ...updatedPageObj };
      });
    } else {
      setPageObj((prevPageObj) => {
        const updatedPageObj = {
          ...prevPageObj,
          [name]: { name: value },
          state: "",
        };
        onAddressChange?.({ ...updatedPageObj });
        return { ...updatedPageObj };
      });
    }
  };

  const handleInputChange = (event: any) => {
    const { name, value } = event.target;
    setPageObj((prevPageObj) => {
      const updatedPageObj = {
        ...prevPageObj,
        [name]: value,
      };
      onAddressChange?.({ ...updatedPageObj });
      return { ...updatedPageObj };
    });
  };

  /*
    -----------------------------------------------------------------------------------------------
    on-Handlers: Event handler for onClick, onToggle etc
    -----------------------------------------------------------------------------------------------
  */

  const onSave = () => {
    if (!validateForm()) {
      return false;
    } else return { ...pageObj };
  };

  const getCurrentValues = () => {
    return { ...pageObj };
  };

  /*
    -----------------------------------------------------------------------------------------------
    Helper
    -----------------------------------------------------------------------------------------------
  */
  const getCurrentAddress = () => {
    return { ...pageObj };
  };
  /* 
    ------------------------------------------------------------------------------------------------
    Sectional Render Functions: For Sub components / section wise renders
    ------------------------------------------------------------------------------------------------
  */

  const renderPropertyType = () => {
    if (props.variant === VARIANTS.TERTIARY) {
      return (
        <div className="col-span-1">
          <SimpleDropDown
            label="table.header.productConfiguration.productDetails.propertyType"
            name="propertyType"
            value={pageObj.propertyType}
            options={propertyTypeOptions}
            optionLabel="name"
            optionValue="code"
            onBlur={validate}
            onChange={handleInputChange}
            validation={simpleValidator?.current?.message(
              "propertyType",
              pageObj.propertyType,
              "mandatory"
            )}
          />
        </div>
      );
    }
    return null;
  };

  const itemTemplate = (item: any) => {
    if (item?.pinCode === undefined) {
      return (
        <div className="flex flex-col items-center justify-center h-39 pointer-events-none cursor-default">
          <Text className="text-primary-labelColor mt-2" label="label.noDataFound" />
        </div>
      );
    }
    return (
      <div className="flex flex-col">
        <Text className="text-primary-labelColor" label={item?.pinCode} />
      </div>
    );
  };

  const itemCityTemplate = (item: any) => {
    if (item?.name === undefined || item?.name?.length < 1) {
      return (
        <div className="flex flex-col items-center justify-center h-39 pointer-events-none cursor-default">
          <Text className="text-primary-labelColor mt-2" label="label.noDataFound" />
        </div>
      );
    }
    return (
      <div className="flex flex-col">
        <Text className="text-primary-labelColor" label={item?.name} />
        <Text
          className="text-secondary-gray-500 text-xxs"
          label={`${item?.name}-${item?.state?.name}`}
        />
      </div>
    );
  };

  const renderMap = () => {
    return (
      <Map
        showMap={showMap}
        ref={mapRef}
        setLatLong={(mapData: any) => {
          setShowMap(false);
          setPageObj((prevPageObj) => ({
            ...prevPageObj,
            longitude: mapData?.longitude,
            latitude: mapData?.latitute,
          }));
        }}
        latLong={{ latitute: pageObj.latitude, longitude: pageObj.longitude }}
      />
    );
  };

  /* 
    -----------------------------------------------------------------------------------------------------
    render: The Main return()
    -----------------------------------------------------------------------------------------------------
  */

  const { address, pincode, city, state, longitude, latitude, smAddressType } = pageObj;

  return (
    <React.Fragment>
      <>{renderMap()}</>

      <AclGuard override={{ read: pageMode === PAGE_MODE.VIEW }}>
        <div className="grid grid-cols-4 gap-2">
          <div className={`col-span-1 ${addressTextAreaClassName ? addressTextAreaClassName : ""}`}>
            <InputTextArea
              name="address"
              className="!min-h-20 !max-h-40 !overflow-y-auto h-25 "
              label={!addressLabel ? "input.organizationLevelBranch.address" : addressLabel}
              placeholder={addressPlaceholder}
              variant={INPUT_STYLE_TYPE.SECONDARY}
              autoResize
              value={address}
              maxCount={!addressMaxCount ? 200 : addressMaxCount}
              onChange={handleInputChange}
              onBlur={validate}
              validation={simpleValidator?.current?.message("address", address, "mandatory")}
            />
          </div>

          <div className="col-span-3">
            <div className="grid grid-cols-3 gap-2">
              <div className="col-span-1">
                <AutoComplete
                  label="input.organizationLevelBranch.pincode"
                  name="pincode"
                  variant={INPUT_STYLE_TYPE.PRIMARY}
                  suggestions={suggestionsTemplate}
                  completeMethod={searchAutoCompletePincode}
                  onFocus={searchAutoCompletePincode}
                  onChange={handleFilterChange}
                  itemTemplate={itemTemplate}
                  loader={isPincodeLoading}
                  value={pincode?.pinCode}
                  onBlur={validate}
                  validation={simpleValidator?.current?.message(
                    "pincode",
                    pincode?.uid,
                    "mandatory"
                  )}
                />
              </div>
              <div className="col-span-1">
                <AutoComplete
                  label="input.organizationLevelBranch.city"
                  name="city"
                  variant={INPUT_STYLE_TYPE.PRIMARY}
                  suggestions={suggestionsTemplate}
                  completeMethod={searchAutoCompleteCity}
                  onFocus={searchAutoCompleteCity}
                  onChange={handleFilterCityStateChange}
                  itemTemplate={itemCityTemplate}
                  loader={isCityLoading}
                  value={city?.name}
                  onBlur={validate}
                  validation={simpleValidator?.current?.message("city", city?.uid, "mandatory")}
                />
              </div>
              <div className="col-span-1">
                <InputBox
                  disabled
                  label="input.organizationLevelBranch.state"
                  name="state"
                  variant={INPUT_STYLE_TYPE.SECONDARY}
                  value={state}
                  onBlur={validate}
                  validation={simpleValidator?.current?.message("state", state, "mandatory")}
                />
              </div>

              {variant === VARIANTS.PRIMARY ? (
                <div className="col-span-1">
                  <SimpleDropDown
                    label="input.basicInformation.typeOfResidential"
                    name="smAddressType"
                    value={smAddressType}
                    optionLabel="name"
                    optionValue="code"
                    options={addressList}
                    onBlur={validate}
                    onChange={handleInputChange}
                    validation={simpleValidator?.current?.message(
                      "smAddressType",
                      smAddressType,
                      "mandatory"
                    )}
                  />
                </div>
              ) : variant === VARIANTS.SECONDARY || variant === VARIANTS.TERTIARY ? (
                <React.Fragment>
                  <div className="col-span-1">
                    <InputBox
                      label={isLatLong ? "input.pincode.longitudeM" : "input.pincode.longitude"}
                      name="longitude"
                      variant={INPUT_STYLE_TYPE.SECONDARY}
                      value={longitude}
                      onChange={handleInputChange}
                      onBlur={validate}
                      validation={
                        isLatLong
                          ? simpleValidator?.current?.message(
                              "longitude",
                              longitude,
                              "mandatory|longitude"
                            )
                          : simpleValidator?.current?.message("longitude", longitude, "longitude")
                      }
                    />
                  </div>

                  <div className="col-span-1">
                    <InputBox
                      label={isLatLong ? "input.pincode.latitudeM" : "input.pincode.latitude"}
                      name="latitude"
                      variant={INPUT_STYLE_TYPE.SECONDARY}
                      value={latitude}
                      onChange={handleInputChange}
                      onBlur={validate}
                      validation={
                        isLatLong
                          ? simpleValidator?.current?.message(
                              "latitude",
                              latitude,
                              "mandatory|latitude"
                            )
                          : simpleValidator?.current?.message("latitude", latitude, "latitude")
                      }
                    />
                  </div>

                  {renderPropertyType()}

                  {!hideMarkUsingMap && (
                    <div className="flex justify-start items-center gap-2">
                      <img
                        src={pageMode === PAGE_MODE.VIEW ? locationDisabledIcon : locationIcon}
                        alt="location-icon"
                      />
                      <TextButton
                        label="label.button.MarkUsingMap"
                        onClick={() => {
                          if (mapRef?.current) {
                            mapRef?.current?.show();
                          }
                        }}
                      />
                    </div>
                  )}
                </React.Fragment>
              ) : null}
            </div>
          </div>
        </div>

        {variant === VARIANTS.PRIMARY ? (
          <div className="grid grid-cols-4 gap-2">
            <div className="col-span-1">
              <InputBox
                label={isLatLong ? "input.pincode.longitudeM" : "input.pincode.longitude"}
                name="longitude"
                variant={INPUT_STYLE_TYPE.SECONDARY}
                value={longitude}
                onChange={handleInputChange}
                onBlur={validate}
                validation={
                  isLatLong
                    ? simpleValidator?.current?.message(
                        "longitude",
                        longitude,
                        "mandatory|longitude"
                      )
                    : simpleValidator?.current?.message("longitude", longitude, "longitude")
                }
              />
            </div>
            <div className="col-span-1">
              <InputBox
                label={isLatLong ? "input.pincode.latitudeM" : "input.pincode.latitude"}
                name="latitude"
                variant={INPUT_STYLE_TYPE.SECONDARY}
                value={latitude}
                onChange={handleInputChange}
                onBlur={validate}
                validation={
                  isLatLong
                    ? simpleValidator?.current?.message("latitude", latitude, "mandatory|latitude")
                    : simpleValidator?.current?.message("latitude", latitude, "latitude")
                }
              />
            </div>
            <div className="flex justify-start items-center gap-2">
              <img
                src={pageMode === PAGE_MODE.VIEW ? locationDisabledIcon : locationIcon}
                alt="location-icon"
              />
              <TextButton
                label="label.button.MarkUsingMap"
                onClick={() => {
                  if (mapRef?.current) {
                    mapRef?.current?.show();
                  }
                }}
                isIgnoreAccessLevel
              />
            </div>
          </div>
        ) : null}

        {props.children}
      </AclGuard>

      <ToastCustom
        position={position}
        ref={toastMessageRef}
        toastText={toastMessage}
        variant={toastMessageVariant}
      />

      <ErrorDialog
        onHide={() => setShowDialogAPIError(false)}
        visible={showDialogAPIError}
        errorMessage={errorMessage}
      />
    </React.Fragment>
  );
});

AddressWithoutMemo.defaultProps = {
  variant: VARIANTS.PRIMARY,
};

const Address = React.memo(AddressWithoutMemo);

export default Address;
